import React from 'react';
import { Form, Input, Button, Image, Modal } from 'antd';
import { useHistory } from "react-router-dom"
import "Container/scss/addpro.scss";

const { TextArea } = Input;
const { confirm } = Modal;
const formItemLayout = {
    labelCol: {
        xs: { span: 22 },
        sm: { span: 6 },
    },
    wrapperCol: {
        xs: { span: 20 },
        sm: { span: 15 },
    },
};
const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 22,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 11,
        },
    },
};


const DetailVoucher = (props) => {

    const [form] = Form.useForm();
    const history = useHistory();
    let voucherID = JSON.parse(localStorage.getItem('voucherID'));
    var dateBD = new Date(voucherID.ngaybd);
    var dateKT = new Date(voucherID.ngaykt);

    const back = () => {
        confirm({
            title: 'Ban mun tr v trang danh sach voucher?',
            okText: 'Tr v',
            okType: 'danger',
            cancelText: 'Khng',
            onOk() {
                localStorage.removeItem("voucherID");
                history.push('/danh-sach-voucher');
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    };


    return (
        <>
            <div className="form-wrapper">
                <h2 style={{ textAlign: 'center' }}>THNG TIN CHI TIT VOUCHER</h2>
                <Form
                    {...formItemLayout}
                    form={form}
                    name="register"
                    scrollToFirstError
                    initialValues={{
                        makm: `${voucherID.makm}`,
                        tenkm: `${voucherID.tenkm}`,
                        voucher: `${voucherID.voucher}`,
                        dieukien: `${voucherID.dieukien}`,
                        giagiam: `${voucherID.giagiam}`,
                        ghichu: `${voucherID.ghichu}`,
                        hinh: `${voucherID.hinh}`,
                    }}
                >
                    <Form.Item
                        name="makm"
                        label="M khuyn mai"
                    >
                        <Input disabled style={{fontWeight: 600, color: 'black'}}/>
                    </Form.Item>
                    <Form.Item
                        name="tenkm"
                        label="Tn khuyn mai"
                    >
                        <Input disabled style={{fontWeight: 600, color: 'black'}}/>
                    </Form.Item>
                    <Form.Item
                        name="voucher"
                        label="M Voucher"
                    >
                        <Input disabled style={{fontWeight: 600, color: 'black'}}/>
                    </Form.Item>
                    <Form.Item
                        name="dieukien"
                        label="iu kin"
                    >
                        <Input disabled style={{fontWeight: 600, color: 'black'}}/>
                    </Form.Item>
                    <Form.Item
                        name="giagiam"
                        label="Gi gim"
                    >
                        <Input disabled style={{fontWeight: 600, color: 'black'}}/>
                    </Form.Item>
                    <Form.Item
                        name="ghichu"
                        label="Ghi ch"
                    >
                        <TextArea rows={3} disabled style={{fontWeight: 600, color: 'black'}}/>
                    </Form.Item>
                    <Form.Item
                        label="Anh san phm"
                    >
                        <Image src={voucherID.hinh} width={120} />
                    </Form.Item>
                    <Form.Item
                        label="Ngay bt u"
                    >
                        <Input value={dateBD.toLocaleDateString()} disabled style={{fontWeight: 600, color: 'black'}}/>
                    </Form.Item>
                    <Form.Item
                        label="Ngay kt thuc"
                    >
                        <Input value={dateKT.toLocaleDateString()} disabled style={{fontWeight: 600, color: 'black'}}/>
                    </Form.Item>
                    <Form.Item
                        label="Trang thai"
                    >
                        {voucherID.trangthai === 1 ? (
                            <Input value="Hin" disabled style={{fontWeight: 600, color: 'black'}}/>
                        ) : (
                            <Input value="n" disabled style={{fontWeight: 600, color: 'black'}}/>
                        )}
                    </Form.Item>
                    <Form.Item {...tailFormItemLayout}>
                        <Button className="ant-btn ant-btn-primary" onClick={back}>
                            Tr v
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        </>
    );
};
export default DetailVoucher;
