import AxiosComment from "./AxiosComment"

const comment = {
    getAll: () => {
        const url = "/";
        return AxiosComment.get(url);
    },
    //(Chi tit 1 bnh lun theo m bnh lun)
    getid: (id) => {
        const url = `/${id}`;
        return AxiosComment.get(url);
    },
    //(Tt c bnh lun theo m sn phm)
    getProductID: (id) => {
        const url = `/san-pham/${id}`;
        return AxiosComment.get(url);
    },
    //(Tt c bnh lun theo m khch hng)
    getUserID: (id) => {
        const url = `/khach-hang/${id}`;
        return AxiosComment.get(url);
    },
    //(Tt c chi tit bnh lun theo m bnh lun)
    getDetailID: (id) => {
        const url = `/${id}/chi-tiet-bluan`;
        return AxiosComment.get(url);
    },
    //(Thm mi 1 bnh lun)
    addComment: (values, token) => {
        const url = "/";
        return AxiosComment.post(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Tr li 1 bnh lun  c sn)
    replyComment: (values, token) => {
        const url = "/tra-loi/admin";
        return AxiosComment.post(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Chnh sa ni dung chi tit(tr li) bnh lun theo m chitietbl)
    updateDeComment: (values, token) => {
        const url = "/cap-nhat-tra-loi/admin";
        return AxiosComment.put(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht trng thi 1 bnh lun theo mabl n hoc hin)
    hideCommet: (values, token) => {
        const url = "/cap-nhat-trang-thai";
        return AxiosComment.put(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Xo 1 bnh lun theo mabl)
    deleteCommentID: (id, token) => {
        const url = `/${id}`;
        return AxiosComment.delete(url, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Xo 1 chi tit(tr li) bnh lun theo m chitietbl)
    deleteCommentDe: (id, token) => {
        const url = `/xoa-tra-loi/admin/${id}`;
        return AxiosComment.delete(url, {
            headers: {
                Token: `${token}`,
            },
        });
    }
};

export default comment;