import React from "react";
import { Menu } from 'antd';
import { AppstoreOutlined, UnorderedListOutlined , SettingOutlined, DashboardOutlined, IdcardOutlined,TagsOutlined, MessageOutlined, 
    ProfileOutlined, DollarCircleOutlined, LogoutOutlined, SkinOutlined  } from '@ant-design/icons';
import { BrowserRouter as Router, useHistory } from "react-router-dom";
import "../Components/scss/nav.scss"


const { SubMenu } = Menu;
const Navigation = () => {
    let history = useHistory()
    const link = useHistory();
    const linkto = (e) => {
        link.push(`/${e.key}`);
    }
    const logout = () => {
        //localStorage.removeItem("token")
        localStorage.removeItem("user")
        history.push('/');
        window.location.reload();
    }
    let result = JSON.parse(localStorage.getItem('user'))
    console.log(result);
    return (
        <>
            <Menu
                /* onClick={this.handleClick} */
                style={{ width: 256, minHeight: 300 }}
                defaultSelectedKeys={['1']}
                defaultOpenKeys={result.permission === "QLNS" ? (['sub8', 'sub2']) : (['sub3', 'sub4'])}
                mode="inline"
                onClick={linkto}
                className="nav"
                forceSubMenuRender="true">
                <Menu.Item key="" icon={<DashboardOutlined />}>Dashboard</Menu.Item>
                {result.permission === "QLNS" || result.permission === "Admin" ? (
                    <SubMenu key="sub2" icon={<IdcardOutlined />} title=" Qun l ti khon">
                        <Menu.Item key="danh-sach-khach-hang" icon={<ProfileOutlined />}>Khch hng</Menu.Item>
                        <Menu.Item key="danh-sach-admin" icon={<ProfileOutlined />}>Nhn vin</Menu.Item>
                    </SubMenu>
                ) : ("")}
                {result.permission === "QLCH" || result.permission === "Admin" || result.permission === "NVBH" ? (
                    <>
                        <SubMenu key="sub3" icon={<AppstoreOutlined />} title="Qun l danh mc">
                            <Menu.Item key="tat-ca-san-pham" icon={<UnorderedListOutlined />} >Danh sch sn phm</Menu.Item>
                            {result.permission !== "NVBH" ? (
                                <>
                                    <Menu.Item key="danh-sach-loai" icon={<UnorderedListOutlined />} >Danh sch loi</Menu.Item>
                                    <Menu.Item key="danh-sach-nha-sx" icon={<UnorderedListOutlined />} >Danh sch nh sn xut</Menu.Item>
                                    <Menu.Item key="danh-muc-san-pham" icon={<UnorderedListOutlined />}>Danh mc sn phm</Menu.Item>
                                </>
                            ) : ("")}
                        </SubMenu>
                        <SubMenu key="sub4" icon={<DollarCircleOutlined />} title="Qun l kinh doanh">
                            <Menu.Item key="danh-sach-thong-ke" icon={<UnorderedListOutlined />} >Danh sch thng k</Menu.Item>
                            <Menu.Item key="danh-sach-don-hang" icon={<UnorderedListOutlined />} >Danh sch n hng</Menu.Item>
                            <Menu.Item key="danh-sach-trang-thai" icon={<UnorderedListOutlined />} >Danh sach trang thai</Menu.Item>
                            <SubMenu key="sub5" icon={<TagsOutlined />} title="Qun l khuyn mi">
                                <Menu.Item key="danh-sach-voucher" icon={<UnorderedListOutlined />}>Danh sch voucher</Menu.Item>
                                <Menu.Item key="danh-sach-khuyen-mai" icon={<UnorderedListOutlined />}>Danh sch khuyn mi</Menu.Item>
                            </SubMenu>
                        </SubMenu>
                        <SubMenu key="sub6" icon={<MessageOutlined />} title="Qun l bnh lun">
                            <Menu.Item key="danh-sach-binh-luan" icon={<UnorderedListOutlined />}>Danh sch cc bnh lun</Menu.Item>
                        </SubMenu>
                        <Menu.Item key="bang-size" icon={<SkinOutlined />}>Bng size qun o</Menu.Item>
                    </>
                ) : ("")}

                <SubMenu key="sub8" icon={<SettingOutlined />} title="Ci t">
                    <Menu.Item key="tai-khoan" icon={<ProfileOutlined />}>Thng tin ti khon</Menu.Item>
                    <Menu.Item onClick={logout} icon={<LogoutOutlined />}>ng xut</Menu.Item>
                </SubMenu>
            </Menu>
        </>
    )
}

export default Navigation;