import AxiosCity from "./AxiosCity";

const city = {
    //(Danh sch tt c Tnh/Thnh ph)
    getAll: () => {
        const url = '/city';
        return AxiosCity.get(url);
    },
    getAll_pickShiftGHN: () => {
        const url = "/ca-lay-hang-GHN";
        return AxiosCity.get(url);
    },
    //(Chi tit 1 thnh ph theo ID)
    getCityID: (id) => {
        const url = `/city/${id}`;
        return AxiosCity.get(url);
    },
    getAll_ServiceGHN: (id) => {
        const url = `/dich-vu-GHN/${id}`;
        return AxiosCity.get(url);
    },
    //(Danh sch ton b Qun/Huyn thuc 1 Tnh/Thnh ph)
    getCityDistrict: (id) => {
        const url = `/city/${id}/district`;
        return AxiosCity.get(url);
    },
    //(Chi tit 1 Qun/Huyn theo ID)
    getDistrictID: (id) => {
        const url = `/district/${id}`;
        return AxiosCity.get(url);
    },
    //(Danh sch ton b Phng/X thuc 1 Qun/Huyn)
    getDistrictWard: (id) => {
        const url = `/district/${id}/ward`;
        return AxiosCity.get(url);
    },
    //(Chi tit 1 Phng/X theo ID)
    getWardID: (id) => {
        const url = `/ward/${id}`;
        return AxiosCity.get(url);
    },
    //(Thng k Dashboard)
    getStatistical: (token) => {
        const url = `/thong-ke`;
        return AxiosCity.get(url, {
            headers: {
                Token: `${token}`,
            },
        });
    }
};

export default city;