import { Button, Form, Input, message, Select } from "antd";
import admin from 'API_Call/Api_admin/admin';
import city from 'API_Call/Api_city/city';
import React, { useEffect, useState } from 'react';
import { Link, useHistory } from "react-router-dom";
import "Container/scss/addpro.scss";

const { Option } = Select;
const formItemLayout = {
    labelCol: {
        xs: { span: 22 },
        sm: { span: 6 },
    },
    wrapperCol: {
        xs: { span: 20 },
        sm: { span: 15 },
    },
};
const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 22,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 10,
        },
    },
};
const AddNV = (props) => {
    const token = localStorage.getItem("token");
    const [form] = Form.useForm();
    const history = useHistory();


    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
            <Select style={{ width: 70 }}>
                <Option value="86">+84</Option>
            </Select>
        </Form.Item>
    );

    //API Thanh Ph
    const [listCity, setlistCity] = useState([]);
    useEffect(() => {
        city.getAll().then((res) => {
            setlistCity(res.data.city);
        })
    }, []);

    //API Qun - Huyn
    const [listDistrict, setlistDistrict] = useState([]);
    let idCity = "";
    const onChangeCity = (e) => {
        idCity = e;
        city.getCityDistrict(idCity).then((res) => {
            setlistDistrict(res.data.district);
        })
    };
    
    //API Phng - Xa
    const [listWard, setlistWard] = useState([]);
    let idDistrict = "";
    const onChangeDistrict = (e) => {
        idDistrict = e;
        city.getDistrictWard(idDistrict).then((res) => {
            setlistWard(res.data.ward);
        })
    };

    const register = (values) => {
        console.log(values);
        admin.register(values, token).then((res) => {
            if (res.data.status ==="Success") {
                message.success(res.data.message)
                setTimeout(() => {
                    history.push('/danh-sach-admin');
                }, 2000)
            }
            else{
                message.error(res.data.message)
            }
        })
            .catch(err => {
                message.error(`Thm nhn vin tht bi !\n ${err.response.data.message}`)
            })
    };

    return (
        <div className="form-wrapper">
            <h2 style={{ textAlign: 'center' }}>THM NHN VIN</h2>
            <Form
                {...formItemLayout}
                form={form}
                name="register"
                onFinish={register}
                initialValues={{
                    residence: ["zhejiang", "hangzhou", "xihu"],
                    prefix: "86",
                }}
                scrollToFirstError
                className="register-form"
            >
                <Form.Item
                    name="email"
                    id="email"
                    label="E-mail"
                    rules={[
                        {
                            type: "email",
                            message: "Vui lng nhp ng E-mail!",
                        },
                        {
                            required: true,
                            message: "Bn cha nhp E-mail !",
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name="name"
                    id="name"
                    label="Tn nhn vin"
                    tooltip="y l tn ng nhp ca bn."
                    rules={[
                        {
                            required: true,
                            message: "Vui lng nhp tn ti khong !!!",
                            whitespace: true,
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    id="pass"
                    name="pass"
                    label="Mt khu"
                    rules={[
                        {
                            required: true,
                            message: "Bn cha nhp mt khu!",
                        },
                    ]}
                    hasFeedback
                >
                    <Input.Password />
                </Form.Item>
                <Form.Item
                    id="pass1"
                    name="pass1"
                    label="Xc nhn mt khu"
                    dependencies={["password"]}
                    hasFeedback
                    rules={[
                        {
                            required: true,
                            message: "Bn phi xc nhn mt khu!",
                        },
                        ({ getFieldValue }) => ({
                            validator(_, value) {
                                if (!value || getFieldValue("pass") === value) {
                                    return Promise.resolve();
                                }

                                return Promise.reject(
                                    new Error(
                                        "Hai mt khu phi ging nhau!"
                                    )
                                );
                            },
                        }),
                    ]}
                >
                    <Input.Password />
                </Form.Item>
                <Form.Item
                    name="city"
                    id="city"
                    label="Thnh ph"
                >
                    <Select onChange={onChangeCity}>
                        {listCity.map((item) => {
                            return (
                                <>
                                    <Option value={item.ID}>{item.Title}</Option>
                                </>
                            )
                        })}
                    </Select>
                </Form.Item>
                <Form.Item
                    name="district"
                    id="district"
                    label="Qun - Huyn"
                >
                    <Select onChange={onChangeDistrict}>
                        {listDistrict.map((item) => {
                            return (
                                <>
                                    <Option value={item.ID}>{item.Title}</Option>
                                </>
                            )
                        })}
                    </Select>
                </Form.Item>
                <Form.Item
                    name="ward"
                    id="ward"
                    label="Phng - X"
                >
                    <Select>
                        {listWard.map((item) => {
                            return (
                                <>
                                    <Option value={item.ID}>{item.Title}</Option>
                                </>
                            )
                        })}
                    </Select>
                </Form.Item>
                <Form.Item
                    name="address"
                    id="address"
                    label="a ch"
                    rules={[{
                        required: true,
                        message: 'Vui lng nhp a ch !'
                    }]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name="phone"
                    id="phone"
                    label="Phone Number"
                    rules={[{
                        required: true,
                        message: 'Vui lng nhp s in thoi !'
                    }]}
                >
                    <Input addonBefore={prefixSelector} style={{ width: '100%' }} />
                </Form.Item>
                <Form.Item
                    name="permission"
                    id="permission"
                    label="M quyn"
                >
                    <Select>
                        <Option value="QLCH">Qun l ca hng</Option>
                        <Option value="NVBH">Nhn vin bn hng</Option>
                        <Option value="NVGH">Nhn vin giao hng</Option>
                    </Select>
                </Form.Item>
                {/* <Form.Item
                        name="set"
                        hidden='true'
                    >
                        <Input value={"123"} id={"123"} defaultValue={"123"} />
                    </Form.Item> */}
                <Form.Item {...tailFormItemLayout}>
                    <Link to={'/danh-sach-admin'} ><p style={{ marginRight: "20px", }} className="ant-btn ant-btn-dashed ">Tr v</p></Link>
                    <Button value="submit" type="primary" htmlType="submit">
                        ng k
                    </Button>
                </Form.Item>
            </Form>
        </div>
    );
}

export default AddNV;