import AxiosCatalog from "./AxiosCatalog"

const catalog = {
    getAll: () => {
        const url = "/";
        return AxiosCatalog.get(url);
    },
    //(Chi tit 1 danh mc theo m danh mc)
    getDanhmucID: (id) => {
        const url = `/${id}`;
        return AxiosCatalog.get(url);
    },
    //(Danh sch tt c loi)
    getAllType: () => {
        const url = "/loai";
        return AxiosCatalog.get(url);
    },
    //(Tt c loi sphm theo m danh mc)
    getTypeDanhmucID: (id) => {
        const url = `/${id}/loai`;
        return AxiosCatalog.get(url);
    },
    //(Chi tit 1 loi theo m loi)
    getTypeID: (id) => {
        const url = `/loai/${id}`;
        return AxiosCatalog.get(url);
    },
    //(Thm mi 1 danh mc sp)
    addCatalog: (values, token) => {
        const url = "/";
        return AxiosCatalog.post(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Thm mi 1 loi sp)
    addProtype: (values, token) => {
        const url = "/loai";
        return AxiosCatalog.post(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht thng tin danh mc theo m danh mc)
    updateCatalog: (values, token) => {
        const url = "/";
        return AxiosCatalog.put(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht thng tin loi theo m loi)
    updateProtype: (values, token) => {
        const url = "/loai";
        return AxiosCatalog.put(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht trng thi loi theo m loi)
    updateStatusType: (values, token) => {
        const url = "/cap-nhat-trang-thai-loai";
        return AxiosCatalog.put(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht trng thi danh mc theo m danh mc)
    updateStatusCata: (values, token) => {
        const url = "/cap-nhat-trang-thai";
        return AxiosCatalog.put(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Xo loi sn phm theo id)
    deleteProtype: (id, token) => {
        const url = `/loai/${id}`;
        return AxiosCatalog.delete(url, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Xo danh mc sn phm theo id)
    deleteCatalog: (id, token) => {
        const url = `/${id}`;
        return AxiosCatalog.delete(url, {
            headers: {
                Token: `${token}`,
            },
        });
    }
};

export default catalog;