import React, { useState, useEffect } from 'react';
import { Image, Row, Col, Input, Button, message, Select, Table } from 'antd';
import Meta from "antd/lib/card/Meta";
import { Link , useHistory} from "react-router-dom";
import "Container/scss/orderDetail.scss";
import moment from 'moment';

const { TextArea } = Input;

const OrderDetail = (props) => {
    let ORDER = JSON.parse(localStorage.getItem("order"));
    const orderStatis = JSON.parse(localStorage.getItem("orderStatis"));
    if(orderStatis !== null){
        ORDER = orderStatis;
    }
    const link = useHistory();

    const back = () => {
        if(orderStatis !== null){
            link.push('/danh-sach-thong-ke');
            localStorage.removeItem("orderStatis");
        }else{
            link.push('/danh-sach-don-hang');
            localStorage.removeItem("order");
        }
    }

    return (
        <div className="wrapper" >
            <Row className="btn-box">
                <Button onClick={back} className="pay" type="primary">
                    Tr v
                </Button>
            </Row>
            <Row className="box">
                <Col className="col-one">
                    <h1>Thng tin n hang</h1>
                    <ul>
                        {/* <li>Ma Khach hang: {ORDER.makh}</li> */}
                        <li><span>M n hng: </span>{ORDER.madonhang}</li>
                        {ORDER.code_GHN === null ? ("") : (<li><span>M GHN: </span>{ORDER.code_GHN}</li>)}
                        <li><span>Tn khach hang: </span>{ORDER.tenkh}</li>
                        <li><span>in thoai: </span>{ORDER.sodienthoai}</li>
                        <li><span>Email: </span>{ORDER.email}</li>
                        <li><span>ia chi: </span>{ORDER.diachi}</li>
                        <li><span>Hinh thc thanh toan: </span>{ORDER.hinhthuc}</li>
                        {ORDER.ghichu === "" ? ("") : (<li><span>Ghi chu: </span>{ORDER.ghichu}</li>)}
                        {/* {ORDER.makm === null ? ("") : (<li><span>M khuyn mi: </span>{ORDER.makm}</li>)} */}
                        <li><span>Ngay t: </span>{moment(ORDER.ngaydat).format('DD/MM/YYYY')}</li>
                        <li><span>Phi vn chuyn: </span>{(ORDER.tienship).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </li>
                        <li><span>Tng hoa n: </span>{(ORDER.tongtien).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </li>
                        {ORDER.ngaygiao === null ? ("") : (<li><span>Ngay giao hng: </span>{moment(ORDER.ngaygiao).format('DD/MM/YYYY')}</li>)}
                        <li><span>Trng thi n hng: </span>{ORDER.tentt}</li>
                    </ul>
                </Col>
                <Col className="col-two">
                    <h3>Chi tit n hang</h3>
                    <ul>
                        {ORDER.chitietDH.map((item) => (
                            <li className="number">
                                <ul>
                                    <li><span>Tn san phm: </span>{item.tensp}</li>
                                    <li><span>S lng: </span>{item.soluong}</li>
                                    <li><span>Gia: </span>{(item.gia).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </li>
                                    {Number(item.giagiam) === 0 ? ("") : (<li><span>Giam gia: </span>{item.giagiam}</li>)}
                                    <li><span>Thanh tin: </span>{(item.thanhtien).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </li>
                                </ul>
                            </li>
                        ))}
                    </ul>

                </Col>
            </Row>

        </div>
    );
}

export default OrderDetail;
