import AxiosDiscount from "./AxiosDiscount"

const discount = {
    getAll: () => {
        const url = "/";
        return AxiosDiscount.get(url);
    },
    //(Danh sch tt c cc khuyn mi theo sn phm)
    getAllSale: () => {
        const url = "/san-pham";
        return AxiosDiscount.get(url);
    },
    //(Danh sch tt c cc khuyn mi l voucher)
    getAllVoucher: () => {
        const url = "/voucher";
        return AxiosDiscount.get(url);
    },
    //(Chi tit ca 1 khuyn mi theo m khuyn mi)
    getSaleID: (id, token) => {
        const url = `/${id}`;
        return AxiosDiscount.get(url, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Chi tit ca 1 voucher theo m voucher)
    getVoucherID: (id, token) => {
        const url = `/voucher/${id}`;
        return AxiosDiscount.get(url, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Thm 1 khuyn mi theo sn phm)
    addSale: (values, token) => {
        const url = "/san-pham";
        return AxiosDiscount.post(url ,values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Thm 1 khuyn mi l voucher)
    addVoucher: (values, token) => {
        const url = "/voucher";
        return AxiosDiscount.post(url ,values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht chng trnh khuyn mi theo sn phm)
    updateSale: (values, token) => {
        const url = "/san-pham";
        return AxiosDiscount.put(url ,values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht chng trnh khuyn mi l voucher)
    updateVoucher: (values, token) => {
        const url = "/voucher";
        return AxiosDiscount.put(url ,values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht trng thi chng trnh khuyn mi)
    updateStatus: (values, token) => {
        const url = "/";
        return AxiosDiscount.put(url ,values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Xo chng trnh khuyn mi theo sn phm)
    deleteSale: (id, token) => {
        const url = `/${id}`;
        return AxiosDiscount.delete(url, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Xo khuyn mi voucher)
    deleteVoucher: (id, token) => {
        const url = `/${id}`;
        return AxiosDiscount.delete(url, {
            headers: {
                Token: `${token}`,
            },
        });
    },
};

export default discount;