import { Button, Form, Input, message, Modal } from 'antd';
import producer from 'API_Call/Api_producer/producer';
import React from 'react';
import { Link, useHistory } from "react-router-dom";
import "Container/scss/addpro.scss";

const formItemLayout = {
    labelCol: {
        xs: { span: 22 },
        sm: { span: 6 },
    },
    wrapperCol: {
        xs: { span: 20 },
        sm: { span: 15 },
    },
};
const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 22,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 10,
        },
    },
};

const AddProducer = (props) => {
    const token = localStorage.getItem("token");
    const [form] = Form.useForm();
    const history = useHistory();
    const { confirm } = Modal;
    const addProducer = (values) => {
        console.log(values);
        producer.addProducer(values, token).then((res) => {
            message.success(res.data.message)
            setTimeout(() => {
                history.push('/danh-sach-nha-sx');
            }, 2000)
        })
            .catch(err => {
                console.log(err.response);
                message.error(`${err.response.data.message}`)
            })
    };
    const back = () => {
        confirm({
            title: 'Ban mun tr v trang danh sach nha san xut?',
            okText: 'Tr v',
            okType: 'danger',
            cancelText: 'Khng',
            onOk() {
                history.push('/danh-sach-nha-sx');
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    };

    return (
        <>
            <div className="form-wrapper">
                <h2 style={{ textAlign: 'center' }}> Nhp thng tin nha san xut</h2>
                <Form
                    {...formItemLayout}
                    form={form}
                    name="addProducer"
                    onFinish={addProducer}
                    scrollToFirstError
                >
                    <Form.Item
                        name="mansx"
                        label="M nha san xut"
                        rules={[
                            {
                                type: 'string',
                                message: 'M nha san xut khng c  trng!',
                            },
                            {
                                required: true,
                                message: 'in m nha san xut',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name="tennsx"
                        label="Tn nha san xut"
                        rules={[
                            {
                                required: true,
                                message: 'Nhp tn nha san xut!',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name="xuatxu"
                        label="Xut x"
                        rules={[
                            {
                                required: true,
                                message: 'Nhp xut x!',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item {...tailFormItemLayout}>
                        <Button className="ant-btn ant-btn-dashed " onClick={back} style={{ marginLeft: -30 }}>
                            Tr v
                        </Button>
                        <Button type="primary" htmlType="submit" style={{ marginLeft: 30 }}>
                            Thm nha san xut
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        </>
    );
};
export default AddProducer;
