import AxiosOrder from "./AxiosOrder"

const order = {
    getAll: () => {
        const url = "/";
        return AxiosOrder.get(url);
    },
    //(Chi tit 1 n hng theo m n hng)
    getOrderID: (id) => {
        const url = `/${id}`;
        return AxiosOrder.get(url);
    },
    //(Danh sch chi tit ca 1 n hng theo m n hng)
    getDetailID: (id) => {
        const url = `/${id}/chi-tiet-dhang`;
        return AxiosOrder.get(url);
    },
    //(Danh sch n hng theo m khch hng)
    getUserID: (id) => {
        const url = `/khach-hang/${id}`;
        return AxiosOrder.get(url);
    },
    // To n hng GHN:
    createOrderGHN: (values, token) => {
        const url = "/GHN/create";
        return AxiosOrder.post(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Thng k n hng)
    getStatistical_Oder: (values, token) => {
        const url = `/thong-ke-don-hang`;
        return AxiosOrder.post(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Cp nht trng thi n hng)
    updateStatus: (values, token) => {
        const url = "/";
        return AxiosOrder.put(url, values, {
            headers: {
                Token: `${token}`,
            },
        });
    },
    //(Hu n hng)
    cancelOrder: (id, token) => {
        const url = `/${id}`;
        return AxiosOrder.delete(url, {
            headers: {
                Token: `${token}`,
            },
        });
    },
};

export default order;